import { Formik } from 'formik';
import React from 'react';
import { Form } from 'react-bootstrap';
import TextField from '../../Login/TextField/TextField';
import * as Yup from 'yup'
import './InfoUser.scss'

const InfoUser = () => {
    const getUserLocal = JSON.parse(localStorage.getItem('users'))

    const validateForm = Yup.object({
        name: Yup.string()
            .max(15, 'Nhp ti a 15 k t')
            .min(4, 'Nhp h v tn ca bn')
            .required('Nhp h v tn ca bn'),
        email: Yup.string()
            .email('Nhp cha ng nh dng email')
            .required('Nhp li email'),
        phone: Yup.number()
            .required('Nhp s in thoi ca bn')
    })

    return (
        <div className="infoUser">
            <Formik
                initialValues={{
                    name: getUserLocal.length === 0 ? '' : getUserLocal[0].name,
                    email: getUserLocal.length === 0 ? '' : getUserLocal[0].email,
                    phone: ''
                }}
                validationSchema={validateForm}
            >
                {() => (
                    <Form>
                        <TextField
                            name="name"
                            label="H v tn"
                            type="text"
                            style=" 50%"

                        />
                        <TextField
                            name="email"
                            label="Email"
                            type="email"
                            style=" 50%"

                        />
                        <TextField
                            name="phone"
                            label="S in thoi"
                            type="number"
                            style=" 50%"

                        />
                        <button>Cp nht</button>
                    </Form>
                )}
            </Formik>
        </div>
    )
}

export default InfoUser
