import React, { Fragment, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import ModalBackdrop from '../../../components/UI/Modal/Modal';
import './CartPayment.scss'

const CartPayment = ({ cart, listUsers }) => {
    const [show, setShow] = useState(false);
    const [modal, setModal] = useState('')

    const handleCloseModal = () => {
        setShow(false)
    }

    const format = (n) => {
        return n.toLocaleString().replace('.', '.').replace(/\d{3}(?=(\d{3})*,)/g)
    }

    const handleClickPay = () => {
        if (listUsers.length === 0) {
            setShow(true)
            setModal(
                <ModalBackdrop
                    title="ng nhp  mua hng bn nh!"
                    right="ng nhp"
                    show={show}
                    link="/login"
                    handleClose={handleCloseModal}
                    displayLeft={{ display: 'none' }}
                    displayRight={{ fontSize: '1.6rem' }}
                    displayTitle={{ fontSize: '3rem' }}
                    displayBody={{ fontSize: '1.4rem' }}
                />
            )
        } else {
            setShow(true)
            setModal(
                <ModalBackdrop
                    title="Cm n qu khch chng ti s lin h sm nht c th "
                    left="Tip tc mua sm"
                    right="Quay li trang ch"
                    show={show}
                    link="/"
                    handleClose={handleCloseModal}
                    displayLeft={{ display: 'none' }}
                    displayRight={{ fontSize: '1.6rem', width: 'fit-content' }}
                    displayTitle={{ fontSize: '3rem' }}
                    displayBody={{ fontSize: '1.4rem' }}
                />
            )
        }

    }

    const totalPrice = cart.reduce((acc, cur) => {
        return acc + cur.price
    }, 0)

    return (
        <Fragment>
            {modal}
            <div className="cartPay">
                <h2>Thanh ton</h2>
                <table>
                    <tr>
                        <td className="payment__title">Tm tnh</td>
                        <td className="payment__normal">{format(totalPrice)} </td>
                    </tr>
                    <tr>
                        <td className="payment__title">Ph vn chuyn</td>
                        <td className="payment__normal">0 </td>
                    </tr>
                    <tr>
                        <td className="payment__title">Khuyn mi</td>
                        <td className="payment__normal">0 </td>
                    </tr>
                    <tr>
                        <td className="payment__title">Thnh tin</td>
                        <td className="payment__total">{format(totalPrice)} </td>
                    </tr>
                </table>
                <button className="btn__pay" onClick={handleClickPay}>Thanh ton</button>
            </div>
        </Fragment>
    )
}

const mapStateToProps = state => {
    return {
        cart: state.CartReducer.cart,
        listUsers: state.LoginReducer.users
    }
}

export default connect(mapStateToProps, null)(CartPayment)
